<template>
	<div>
		<div class="goods">
			<div class="menu-warpper" ref="menu">
				<ul>
					<li v-for="(item,index) in goods" class="menu-item" :class="{'current':currentIndex===index}" @click="selectmenu(index,$event)">
						<span class="text border-1px">
							<span v-show="item.type>0" class="icon" :class="classMap[item.type]"></span>
							{{item.name}}
						</span>
					</li>
				</ul>
			</div>
			<div class="foods-warpper" ref="foods">
				<ul>
					<li v-for="item in goods" class="food-list food-list-hook">
						<h1>{{item.name}}</h1>
						<ul>
							<li  v-for="food in item.foods" @click="selectFood(food,$event)" class="food-item border-1px">
								<div class="icon">
									<img :src="food.icon" width="57" height="57" />
								</div>
								<div class="content">
									<h2 class="name">{{food.name}}</h2>
									<p class="desc">{{food.description}}</p>
									<div class="extra">
										<span>月售{{food.sellCount}}份</span>
										<span>好评率{{food.rating}}%</span>
									</div>
									<div class="price">
										<span class="now">￥{{food.price}}</span><span class="old" v-show="food.oldPrice">￥{{food.oldPrice}}</span>
									</div>
									<div class="carcontrol-warpper">
										<carcontrol :food="food" @cartadd="_drop"></carcontrol>
									</div>
									
								</div>
							</li>
						</ul>
					</li>
				</ul>
			</div>
			<shopcart ref="shopcart" :select-foods="selectfoods" :delivery-price="seller.deliveryPrice" :min-price="seller.minPrice"></shopcart>
		</div>
		<food  @cartadd="_drop" :food="selectedFood" ref="fooddetail"></food>
	</div>
	

</template>

<script>
	import BScroll from "better-scroll";
	import shopcart from "@/components/shopcar/shopcar"
	import carcontrol from "@/components/carContral/carbtn"
	import food from "@/components/food/food"
	const ERR_OK=0;
	export default{
		props:{
			seller:{
				type:Object
			}
		},
		data(){
			return {
				goods:[],
				menuScroll:null,
				foodsScroll:null,
				listHeight:[],
				scrollY:0,
				selectedFood:{}
			}
		},
		computed:{
			currentIndex(){
//				console.log(this.scrollY)
				for(var i=0;i<this.listHeight.length;i++){
					var height= this.listHeight[i];
					var height2=this.listHeight[i+1];
					if(!height2||(this.scrollY>=height&&this.scrollY<height2)){
						return i
					}
				}
			},
			selectfoods(){
				var foods=[];
				this.goods.forEach(function(Val){
					Val.foods.forEach(function(food){
						if(food.count){
							foods.push(food)
						}
					})
				})
				return foods
			}
		},
		created(){
			this.classMap=['decrease','discount','guarantee','invoice','special'];
			this.$http.get("/api/goods").then(function(data){
				if(data.body.errno==ERR_OK){
				this.goods=data.body.data;
				console.log(this.$refs.menu);
				this.$nextTick(()=>{this._initScroll();this._calcuHeight()});
			}
			})
		},
		methods:{
			selectFood(index,event){
				if(!event._constructed){
					return
				}
				this.selectedFood = index;
				this.$refs.fooddetail.show();
			},
			_initScroll(){
				
				this.menuScroll= new BScroll(this.$refs.menu,{
					click:true
				});
				this.foodsScroll =new BScroll (this.$refs.foods,{
					click:true,
					probeType:3
				});
				this.foodsScroll.on('scroll',(pos)=>{
					
					this.scrollY=Math.abs(Math.round(pos.y));
					
				})
			},
			_calcuHeight(){
				var foodList=this.$refs.foods.getElementsByClassName("food-list-hook");
				var height=0;
				this.listHeight.push(height)
				for (var i=0;i<foodList.length;i++) {
					var item =foodList[i]
					height+=item.clientHeight;
					this.listHeight.push(height);
				}
				console.log(this.listHeight)
			},
			selectmenu(index,event){
				if(!event._constructed){
					return
				}
//				console.log(index)
				var foodlist=this.$refs.foods.getElementsByClassName("food-list-hook");
				var el = foodlist[index];
				this.foodsScroll.scrollToElement(el,300)
			},
			_drop(target){
				console.log(target)
				this.$refs.shopcart.drop(target)
			}
		},
		components:{
			shopcart,
			carcontrol,
			food
		},
		events:{
			'cartadd'(target){
				this._drop(target);
				console.log(1)
			}
		}
	};
</script>

<style lang='less'>
	@import url("../../common/style/index.less");
	.goods{
		display: flex;
		position: absolute;
		top: 174px;
		bottom:46px;
		width: 100%;
		overflow: hidden;
		.menu-warpper{
			flex: 0 0 80px;
			width: 80px;
			background-color: #f3f5f7;
			.menu-item{
				display: table;
				height: 54px;
				width: 56px;
				line-height: 14px;
				padding:0 12px;
				&.current{
					position:relative;
					z-index:10;
					margin-top:-1px;
					font-weight:700;
					background:#fff;
					.text{
						border: none;
					}
				}
				.icon{
					display: inline-block;
					width: 16px;
					height: 16px;
					vertical-align: top;
					margin-right: 6px;
					background-size: 16px 16px;
					background-repeat: no-repeat;
					&.decrease{
						.bg-image("decrease_3");
					}
					&.discount{
						.bg-image("discount_3");
					}
					&.guarantee{
						.bg-image("guarantee_3");
					}
					&.invoice{
						.bg-image("invoice_3");
					}
					&.special{
						.bg-image("special_3");
					}
				}
				.text{
					display: table-cell;
					width: 56px;
					vertical-align: middle;
					font-size: 12px;
					.borderpx(rgba(7,17,27,.1));
				}
			}
		}
		.foods-warpper{
			flex: 1;
			h1{
				padding-left:14px;
				height:26px;
				line-height:26px;
				border-left:2px solid #d9dde1;
				font-size:12px;
				color:rgb(147,156,159);
				background:#f3f5f7;
			}
			.food-item{
				display: flex;
				margin: 18px;
				padding-bottom: 18px;
				.borderpx(rgba(7,17,27,.1));
				&:last-child{
					border:none;
					margin-bottom: 0;
				}
				.icon{
					flex: 0 0 57px;
					margin-right:10px ;
				}
				.content{
					flex: 1;
					.name{
						font-weight: 500;
						margin: 2px 0px 8px 0;
						height: 14px;
						line-height: 14px;
						font-size: 14px;
						color: rgb(7,17,27);
					}
					.desc,.extra{
						line-height: 10px;
						font-size: 14px;
						color:rgb(147,153,159);
					}
					.desc{
						margin-bottom: 8px;
						line-height: 14px;
					}
					.extra{
						span:nth-of-type(1){
							margin-right: 12px;
						}
					}
					.price{
						font-weight: 700;
						line-height: 24px;
						.now{
							margin-right: 18px;
							font-size: 14px;
							color: rgb(240,20,20);
						}
						.old{
							text-decoration: line-through;
							font-size: 10px;
							color: rgb(147,153,159);
						}
					}
					.carcontrol-warpper{
						position: absolute;
						right: 0;
						bottom: 12px;
					}
				}
			}
		}
	}
</style>


