<template>
<div class="ratingselect">
	<div class="rating-type">
		<span>{{desc.all}}</span>
		<span>{{desc.positive}}</span>
		<span>{{desc.negative}}</span>
	</div>
	<div class="switch">
		<span class="icon-check_circle"></span>
		<span></span>
	</div>
</div>
</template>

<script>
//	const POSITIVE = 0;
//	const NEGATIVE = 1;
	const ALL = 2
	export default{
		props:{
			ratings:{
				type:Array,
				default(){
					return []
				}
			},
			selectType:{
				type:Number,
				default:ALL
			},
			onlContent:{
				type:Boolean,
				default:false
			},
			desc:{
				type:Object,
				default(){
					return {
						all:'全部',
						positive:"满意",
						negative:"不满意"
					}
				}
			}
		},
		created(){
			console.log(this.desc)
		}
	}
</script>

<style>
</style>


