<template>
<div>
	<div class="shopcart">
		<div class="content" >
			<div class="content-left" @click="toggleList">
				<div class="logo-warpper ">
					<div class="logo" :class="{'highlight':totalCount>0}">
						<i class="icon-shopping_cart"></i>
					</div>
					<div class="num" v-show="totalCount>0">
						{{totalCount}}
					</div>
				</div>
				<div class="price" :class="{'highlight':totalPrice>0}">￥{{totalPrice}}</div>
				<div class="desc">另需配送费￥{{deliveryPrice}}元</div>
			</div>
			<div class="content-right" @click="pay">
				<div class="pay" :class="payClass">
					{{payDes}}
				</div>
			</div>
		</div>
		<div class="ball-container">
			<transition name="drop" v-for="(ball,index) in balls" :key="index" @before-enter="beforeEnter" @enter="enter" @after-enter="afterEnter">
				<div class="ball"  v-show="ball.show" >
					<div class="inner inner-hook"></div>
				</div>
			</transition>
				
		</div>
		<transition name="fold">
			<div class="shopcart-list" v-show="listshow">
				<div class="list-header">
					<h1 class="title">购物车</h1>
					<span class="empty" @click="empty">清空</span>
				</div>
				<div class="list-content" ref="lists">
					<ul>
						<li class="food" v-for="food in selectFoods">
							<span class="name">{{food.name}}</span>
							<div class="price">
								<span>￥{{food.price*food.count}}</span>
							</div>
							<div class="cartcontrol-warpper">
								<carcontrol :food="food" @cartadd="_drop"></carcontrol>
							</div>
						</li>
					</ul>
				</div>
			</div>
		</transition>
	</div>
	<transition name="fade">
		<div class="list-mask" v-show='listshow' @click="holdList"></div>
	</transition>
		
</div>
</template>

<script>
	import BScroll from "better-scroll";
	import carcontrol from "@/components/carContral/carbtn"
	export default{
		props:{
			selectFoods:{
				type:Array,
				default(){
					return [
						{
							price:10,
							count:1
						}
					]
				}
			},
			deliveryPrice:{
				type:Number,
				default:0
			},
			minPrice:{
				type:Number,
				default:0
			}
		},
		data(){
			return{
				balls:[
					{
						show:false
					},
					{
						show:false
					},
					{
						show:false
					},
					{
						show:false
					},
					{
						show:false
					}
				],
				dropBalls:[],
				fold:true,
				
			}
		},
		components:{
			carcontrol
		},
		methods:{
			_drop(target){
				this.drop(target)
			},
			drop(el){
				console.log(el)
				for(var i=0;i<this.balls.length;i++){
					var ball=this.balls[i];
					if(!ball.show){
						ball.show=true;
						ball.el=el;
						this.dropBalls.push(ball);
						return;
					}
				}
			},
			holdList(){
				this.fold= true
			},
			pay(){
				if(this.totalPrice<this.minPrice){
					return
				}
				window.alert("支付"+this.totalPrice+"元")
			},
			beforeEnter(el){
				var count = this.balls.length;
				while(count--){
					var ball = this.balls[count];
					if(ball.show){
						var rect = ball.el.getBoundingClientRect();
						var x = rect.left-32;
						var y=-(window.innerHeight - rect.top - 22);
						el.style.display='';
						el.style.webkitTransform=`translateY(${y}px)`;
						el.style.transform=`translateY(${y}px)`;
						var inner=el.getElementsByClassName('inner-hook')[0];
						inner.style.webkitTransform=`translateX(${x}px)`;
						inner.style.transform=`translateX(${x}px)`;
					}
				}
			},
			enter(el,done){
//				console.log(el.offsetHeight);
				let rf = el.offsetHeight;
				this.$nextTick(()=>{
					el.style.webkitTransform='translateY(0)';
					el.style.transform='translateY(0)';
					var inner=el.getElementsByClassName('inner-hook')[0];
					inner.style.webkitTransform='translateX(0)';
					inner.style.transform='translateX(0)';
					el.addEventListener('transitionend', done);
				})
			},
			afterEnter(el){
				console.log(3)
				var ball = this.dropBalls.shift();
				if(ball){
					ball.show=false;
					el.style.display = 'none'
				}
			},
			toggleList(){
				if(!this.totalCount){
					return 
				}
				this.fold = !this.fold
			},
			empty(){
				this.selectFoods.forEach(function(Val){
					Val.count=0;
				})
			}
			
		},
		computed:{
			totalPrice(){
				var total=0;
				this.selectFoods.forEach(function(Val){
					total+=Val.price*Val.count;
				})
				return total;
			},
			totalCount(){
				var count=0;
				this.selectFoods.forEach(function(Val){
					count+=Val.count;
				})
				return count;
			},
			payDes(){
				if(this.totalPrice===0){
					return `￥${this.minPrice}元起送`;
				}else if(this.totalPrice<this.minPrice){
					var diff=this.minPrice-this.totalPrice;
					return `还差￥${diff}元起送`
				}else{
					return '去结算'
				}
			},
			payClass(){
				if(this.totalPrice<this.minPrice){
					return 'not-enough'
				}else{
					return 'enough'
				}
			},
			listshow(){
				if(!this.totalCount){
					this.fold=true;
					return false
				}
				var show = !this.fold;
				if(show){
					this.$nextTick(()=>{
						if(!this.scroll){
							this.scroll= new BScroll(this.$refs.lists,{
								click:true
							})	
						}else{
							this.scroll.refresh();
						}
					})
				}
				return show
			}
		}
	}
</script>

<style lang="less">
	@import url("../../common/style/index.less");
	.shopcart{
		position: fixed;
		bottom: 0;
		left: 0;
		z-index: 50;
		width: 100%;
		height: 48px;
		.content{
			background-color: #141d27;
			display: flex;
			.content-left{
				flex: 1;
				font-size: 0;
				.logo-warpper{
					display: inline-block;
					position: relative;
					top:-10px;
					margin: 0 12px;
					padding: 6px;
					width: 56px;
					height: 56px;
					box-sizing: border-box;
					vertical-align: top;
					border-radius: 50%;
					background-color: #141d27;
					.logo{
						width: 100%;
						height: 100%;
						border-radius: 50%;
						background:#2b343c;
						text-align: center;
						&.highlight{
							background: rgb(0,160,220);
							.icon-shopping_cart{
								color:#fff
							}
						}
						.icon-shopping_cart{
							line-height: 44px;
							font-size: 24px;
							color: #80858a;
						}
					}
					.num{
						position: absolute;
						top: 0;right: 0;
						width: 24px;
						height: 16px;
						line-height: 16px;
						text-align: center;
						border-radius: 16px;
						font-size: 9px;
						font-weight: 700;
						color: #fff;
						background: rgb(240,20,20);
						box-shadow: 0 4px 8px 0 rgba(0,0,0,.4);
					}
				}
				.price{
					display: inline-block;
					vertical-align: top;
					margin-top: 12px;
					line-height: 24px;
					box-sizing: border-box;
					padding-right: 12px;
					color: rgba(255,255,255,.4);
					border-right: 1px solid rgba(255,255,255,.1);
					font-size: 16px;
					font-weight: 700;
					&.highlight{
						color: #fff;
					}
				}
				.desc{
					display: inline-block;
					vertical-align: top;
					line-height: 24px;
					margin: 12px 0 0 12px;
					font-size: 10px;
					color: rgba(255,255,255,.4);
				}
			}
			.content-right{
				flex:0 0 105px;
				width: 105px;
				.pay{
					height: 48px;
					line-height: 48px;
					text-align: center;
					font-size: 12px;
					color: rgba(255,255,255,.4);
					font-weight: 700;
					background-color: #2b333b;
					&.not-enough{
						background-color: #2b333b;
					}
					&.enough{
						background-color: #00b43c;
						color: #fff;
					}
				}
			}
		}
		.ball-container{
			.ball{
				position: fixed;
				left: 32px;
				bottom: 22px;
				z-index: 200;
				transition: all .6s  cubic-bezier(0.49, -0.29, 0.75, 0.41);
				.inner{
						width: 16px;
						height: 16px;
						border-radius: 50%;
						background-color:rgb(0,160,220);
						transition: all .6s;
					}
			}
		}
		.shopcart-list{
			position: absolute;
			bottom:100%;
			left: 0;
			z-index: -1;
			width:100%;
			transition: all 0.5s;
			.list-header{
				height: 40px;
				line-height: 40px;
				padding: 0 18px;
				background-color: #f3f5f7;
				border-bottom:  1px solid rgba(7,17,27,0.1);
				.title{
					float: left;
					font-size: 14px;
					color: rgb(7,17,27);
				}
				.empty{
					float: right;
					font-size: 12px;
					color: rgb(0,160,220);
				}
			}
			.list-content{
				padding: 0 18px;
				max-height: 217px;
				overflow: hidden;
				background-color: #fff;
				.food{
					position: relative;
					padding: 12px 0;
					box-sizing: border-box;
					.borderpx(rgba(7,17,27,0.1));
					.name{
						line-height: 24px;
						font-size: 14px;
						color: rgb(7,17,27);
					}
					.price{
						position: absolute;
						right: 90px;
						bottom: 12px;
						line-height: 24px;
						font-size: 14px;
						font-weight: 700;
						color: rgb(240,20,20);
					}
					.cartcontrol-warpper{
						position: absolute;
						right: 0;
						bottom: 6px;
					}
				}
				
			}
		}
	}
	.list-mask{
		position: fixed;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		z-index: 40;
		opacity: 1;
		background-color: rgba(7,17,27,0.6);
		backdrop-filter:blur(10px);
		transition: all 0.5s;
	}
	.fold-enter-active,.fold-leave{
		transform: translate3d(0,0%,0);
	}
	.fold-leave-active,.fold-enter{
		transform: translate3d(0,100%,0);
	}
	
	.fade-enter-active,.fade-leave{
		opacity: 1;
		background-color: rgba(7,17,27,0.6);
	}
	.fade-leave-active,.fade-enter{
		opacity: 0;
		background-color: rgba(7,17,27,0);
	}
</style>


