<template>
	<transition name="fooddetail" >
		<div v-show="showFlag" class="food" ref="food">
			<div class="food-content">
				<div class="image-header">
					<img :src="food.image"/>
					<div class="goback" @click="hide">
						<i class="icon-arrow_lift"></i>
					</div>
				</div>
				<div class="content">
					<h1>{{food.name}}</h1>
					<div class="detail">
						<span class="sell-count">月售{{food.sellCount}}份</span>
						<span class="rating">好评率{{food.rating}}</span>
					</div>
					<div class="price">
						<span class="now">￥{{food.price}}</span><span class="old" v-show="food.oldPrice">￥{{food.oldPrice}}</span>
					</div>
					<div class="cartcontrol-warpper">
						<carcontrol :food="food" @cartadd="_drop"></carcontrol>
					</div>
					<div @click="addFirst" class="buy" v-show="!food.count||food.count==0">加入购物车</div>
				</div>
				<split v-show="food.info"></split>
				<div class="info" v-show="food.info">
					<h1>商品简介</h1>
					<p class="text" >{{food.info}}</p>
				</div>
				<split></split>
				<div class="rating">
					<h1>商品评价</h1>
					<rating :select-type="selectType" :only-content:"onlyContent" :desc="desc" :ratings="food.ratings"></rating>
				</div>
			</div>
		</div>
	</transition>
</template>

<script>
	import Vue from "vue"
	import BScroll from "better-scroll";
	import carcontrol from "@/components/carContral/carbtn"
	import split from "@/components/split/split"
	import rating from "@/components/rating-select/ratingSelect"
	//	const POSITIVE = 0;
	//	const NEGATIVE = 1;
	const ALL = 2
	export default{
		props:{
			food:{
				type:Object
			}
		},
		data(){
			return {
				showFlag:false,
				selectType:ALL,
				onlyContent:true,
				desc:{
					all:'全部',
					positive:"推荐",
					negative:"吐槽"
				}
			}
		},
		methods:{
			_drop(target){
				this.$emit('cartadd',target);
			},
			show(){
				this.showFlag=true;
				this.selectType=ALL;
				this.onlyContent=true;
				this.$nextTick(()=>{
					if(!this.scroll){
						this.scroll= new BScroll(this.$refs.food,{
							click:true
						})
					}else{
						this.scroll.refresh();
					}
				})
			},
			hide(){
				this.showFlag=false;
			},
			addFirst(event){
				if(!event._constructed){
					return
				}
				this.$emit('cartadd',event.target);
				Vue.set(this.food,'count',1)
			}
		},
		components:{
			carcontrol,
			split,
			rating
		}
	}
</script>

<style lang="less">
	.food{
		position: fixed;
		left: 0;
		top: 0;
		bottom: 48px;
		z-index: 30;
		width: 100%;
		background-color: #fff;
		transition: all 0.3s;
		.food-content{
			.image-header{
				position: relative;
				width: 100%;
				height: 0;
				padding-top: 100%;
				img{
					position: absolute;
					top: 0;
					left: 0;
					width: 100%;
					height: 100%;
				}
				.goback{
					position: absolute;
					top: 10px;
					left: 0;
					i{
						display: block;
						padding: 10px;
						font-size: 20px;
						color: #fff;
					}
				}
			}
			.content{
				position: relative;
				padding: 18px;
				h1{
					line-height: 14px;
					margin-bottom: 8px;
					font-size: 14px;
					font-weight: 700;
					color:rgb(7,17,27)
				}
				.detail{
					margin-bottom: 18px;
					line-height: 10px;
					font-size: 0;
					.sell-count,.rating{
						font-size: 10px;
						color: rgb(147,153,159);
					}
					.sell-count{
						margin-right: 12px;
					}
				}
				.price{
						font-weight: 700;
						line-height: 24px;
						.now{
							margin-right: 18px;
							font-size: 14px;
							color: rgb(240,20,20);
						}
						.old{
							text-decoration: line-through;
							font-size: 10px;
							color: rgb(147,153,159);
						}
					}
			}
			.cartcontrol-warpper{
				position: absolute;
				right: 12px;
				bottom: 12px;
			}
			.buy{
				position: absolute;
				right: 18px;
				bottom: 18px;
				z-index: 10;
				height: 24px;
				line-height: 24px;
				padding: 0 12px;
				box-sizing: border-box;
				font-size: 10px;
				border-radius: 10px;
				color: #fff;
				background-color: rgb(0,160,220);
			}
		}
		.info{
			padding: 18px;
			h1{
				line-height: 14px;
				margin-bottom: 16px;
				font-size: 14px;
				color: rgb(7,17,27);
			}
			.text{
				line-height: 24px;
				padding: 0 8px;
				font-size: 12px;
				color: rgb(77,85,93);
			}
		}
	}
	.fooddetail-enter-active,.fooddetail-leave{
		transform: scale(1);
	}
	.fooddetail-leave-active,.fooddetail-enter{
		transform: scale(0);
	}
</style>


