<template>
	<div class="carcontrol">
		<transition name="move">
			<div class="car-reduce icon-remove_circle_outline" v-show="food.count>0" @click.stop="reduceCart"></div>
		</transition>
		<transition name="move">
			<div class="car-count" v-show="food.count>0">{{food.count}}</div>
		</transition>

		<div class="car-add icon-add_circle" @click.stop="addCart"></div>
	</div>
</template>

<script>
	import Vue from 'vue'
	export default{
		props:{
			food:{
				type:Object
			}
		},
		
		created(){
//			console.log(this.food)
		},
		methods:{
			addCart(event){
				if(!event._constructed){
					return
				}
				if(!this.food.count){
					Vue.set(this.food,'count',1)
				}else{
					this.food.count++
				}
				this.$emit('cartadd',event.target);
			},
			reduceCart(event){
				if(!event._constructed){
					return
				}
				if(this.food.count){
					this.food.count--
				}
			}
		}
	}
</script>

<style lang="less">
	.carcontrol{
		font-size: 0;
		.car-reduce,.car-add{
			display: inline-block;
			padding: 6px;
			line-height: 24px;
			font-size: 24px;
			color: rgb(0,160,220);
		}
		.car-reduce{
			transition: all 0.5s;
		}
		.car-count{
			display: inline-block;
			vertical-align: top;
			width: 12px;
			padding-top: 6px;
			line-height: 24px;
			font-size: 10px;
			color: rgb(147,153,159);
			transition: all 0.5s;
		}
		.car-add{
			display: inline-block;
		}
	}
.move-enter-active,.move-leave{
		opacity: 1;
		transform: translateX(0px) rotateZ(0deg);
/*		transform-origin:80% 20%;*/
	}
	.move-leave-active,.move-enter{
		opacity: 0;
		transform: translateX(24px) rotateZ(180deg);
/*		transform-origin:90% 10%;*/
	}
</style>


